import React, { useState, useEffect } from "react";
import { Container, Row, Col, Table, Alert } from "react-bootstrap";
import styled from "styled-components";
import axios from "axios";
import moment from "moment";
import { css } from "@emotion/core";
import CircleLoader from "react-spinners/CircleLoader";

const languages = [
  {
    code: "es",
    text: "English-Spanish",
  },
  {
    code: "de",
    text: "English-German",
  },

  {
    code: "it",
    text: "English-Italian",
  },
  {
    code: "el",
    text: "English-Greek",
  },
  {
    code: "fr",
    text: "English-French",
  },
  {
    code: "sv",
    text: "English-Swedish",
  },
];
const REACT_APP_API = "https://mt.eval.covid19.pangeamt.com/api";
const Ul = styled.ul`
  margin-top: 30px;
  list-style: none;
  li {
    margin: 0 10px;
    cursor: pointer;
  }
  li.active {
    color: #9f45ff;
    font-weight: bold;
  }
`;

const override = css`
  display: block;
  margin: 0 auto;
  border-color: red;
  margin-top: 60px;
`;

const App = () => {
  const [lang, setLang] = useState("es");
  const [show, setShow] = useState(false);
  const [files, setFiles] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    fetch();
  }, []);

  const fetch = async () => {
    try {
      setLoading(true);
      const {
        data: { files },
      } = await axios.get(`${REACT_APP_API}`);

      if (files.length === 0) {
        setShow(true);
        setTimeout(() => {
          fetch();
        }, 5000);
      } else {
        setFiles(files);
        setShow(false);
        setLoading(false);
      }
    } catch (error) {
      console.log(error.message);
      setLoading(false);
    }
  };

  const getFiles = (constrained) => {
    const array = [];
    if (files.length) {
      files.forEach((item) => {
        const op = constrained ? "1" : "0";
        if (item.constrained === op && item.tgt === lang) {
          array.push(item);
        }
      });

      for (let i = 0; i < array.length - 1; i++) {
        for (let j = i + 1; j < array.length; j++) {
          if (parseFloat(array[j].BLEU) > parseFloat(array[i].BLEU)) {
            const aux = array[j];
            array[i] = array[j];
            array[j] = aux;
          }
        }
      }
    }

    return array;
  };

  return (
    <div className="App">
      <header id="headerTask3" className="App-header">
        <div class="inner">
          <div class="row d-flex justify-content-center">
            <i class="fas fa-language fa-4x fa-border" aria-hidden="true"></i>
          </div>
          <div class="row d-flex justify-content-center">
            <h2>Machine Translation</h2>
          </div>
          <div class="row d-flex justify-content-center">
            <h4>
              <a href="http://eval.covid19-mlia.eu/task3/" target="_blank">
                Covid-19 MLIA <i class="fas fa-virus" aria-hidden="true"></i>{" "}
                Eval&nbsp;&nbsp;
                <small className="round">(Round1)</small>
              </a>
            </h4>
          </div>
        </div>
      </header>
      <CircleLoader
        css={override}
        size={150}
        color={"#7f00ff"}
        loading={loading}
      />
      {loading && show && (
        <Row className="d-flex justify-content-center">
          <h5>Updating scores...</h5>
        </Row>
      )}

      {!loading && (
        <Container>
          <Row>
            <Col>
              <Ul className="d-flex justify-content-center">
                {languages.map((item, i) => {
                  return (
                    <React.Fragment>
                      <li
                        onClick={() => {
                          setLang(item.code);
                        }}
                        className={lang === item.code ? "active" : ""}
                      >
                        <span key={`key-${item.code}`}>{item.text}</span>
                      </li>
                      {i < languages.length - 1 && <div className="divider" />}
                    </React.Fragment>
                  );
                })}
              </Ul>
              {files.length > 0 && (
                <Row className="d-flex justify-content-center">
                  <span class="badge badge-info">Last update:</span>&nbsp;
                  {moment(files[0].updatedAt).utc().format("LLLL")} UTC
                </Row>
              )}
            </Col>
          </Row>
          <Row className="mt-2">
            <Col>
              <h4>
                Constrained <small>(en-{lang})</small>
              </h4>

              <Table striped bordered hover size="sm">
                <thead>
                  <tr>
                    <th>#</th>
                    <th>Team</th>
                    <th>Description</th>
                    <th style={{ width: 150 }}>BLEU score</th>
                    <th style={{ width: 150 }}>ChrF score</th>
                  </tr>
                </thead>
                <tbody>
                  {getFiles(true).map((item, index) => {
                    return (
                      <tr>
                        <td>{index + 1}</td>
                        <td>{item.Team.team}</td>
                        <td>{item.description}</td>
                        <td>{item.BLEU ? item.BLEU : "Loading..."}</td>
                        <td>{item.ChrF ? item.ChrF : "Loading..."}</td>
                      </tr>
                    );
                  })}
                </tbody>
              </Table>
              {getFiles(true).length === 0 && (
                <Row className="d-flex justify-content-center">
                  <Alert style={{ marginTop: -10 }} variant="warning">
                    No Data!
                  </Alert>
                </Row>
              )}
            </Col>
          </Row>
          <Row className="mt-2">
            <Col>
              <h4>
                Unconstrained <small>(en-{lang})</small>
              </h4>

              <Table striped bordered hover size="sm">
                <thead>
                  <tr>
                    <th>#</th>
                    <th>Team</th>
                    <th>Description</th>
                    <th style={{ width: 150 }}>BLEU score</th>
                    <th style={{ width: 150 }}>ChrF score</th>
                  </tr>
                </thead>
                <tbody>
                  {getFiles(false).map((item, index) => {
                    return (
                      <tr>
                        <td>{index + 1}</td>
                        <td>{item.Team.team}</td>
                        <td>{item.description}</td>
                        <td>{item.BLEU ? item.BLEU : "Loading..."}</td>
                        <td>{item.ChrF ? item.ChrF : "Loading..."}</td>
                      </tr>
                    );
                  })}
                </tbody>
              </Table>
              {getFiles(false).length === 0 && (
                <Row className="d-flex justify-content-center">
                  <Alert style={{ marginTop: -10 }} variant="warning">
                    No Data!
                  </Alert>
                </Row>
              )}
            </Col>
          </Row>
        </Container>
      )}
      <Row className="mt-5 w-100 d-flex justify-content-center footer-app">
        <p>
          Powered by{" "}
          <a href="https://www.pangeanic.com/" target="_blank">
            PangeaMTÂ©
          </a>{" "}
          2020.
        </p>
      </Row>
    </div>
  );
};

export default App;
